.friendMessage {
    padding: 0 70px;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.friendImageAndText {
    display: flex;
    align-items: center;
    gap:25px
}

.friendImageAndText img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.friendText {
    background-color: #F5F7FB;
    display: inline-block;
    max-width: 575px;
    padding: 10px;
    /*border: 1px solid #ccc;*/
    /*border-radius: 10px 10px 10px 10px;*/
    border-radius: 10px;
    box-shadow: 10px 10px 5px #e6e6e6;
    text-wrap: pretty;

}

