.message {
    padding-right: 70px;
    display: flex;
    padding-top: 25px;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
}

.imageAndText {
    display: flex;
    align-items: center;
    gap: 10px;
    flex-direction: row-reverse;
}

.imageAndText img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.text {
    background-color: #0066CC;
    display: block;
    padding: 5px 20px;
    border: 1px solid #ccc;
    /*border-radius: 10px 10px 0 10px;*/
    border-radius: 10px;
    max-width: 575px;
    text-wrap: pretty;
    box-sizing: border-box;
    position: relative;
    box-shadow: 10px 10px 5px #e6e6e6;

}

