.name {
    display: flex;
    justify-content: flex-end;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 16px;
    color: white;
}

.messageText {
    font-family: 'Montserrat', sans-serif;
    font-weight: normal;
    font-size: 16px;
    color: white;
    /*text-wrap: pretty;*/
    width: 100%;
    word-break: break-all;
    white-space: normal;
}

